@import 'mixins';
@import 'variables';
@import 'desktop';
@import 'responsive';
@import '../fonts/stylesheet.css';

// Basic Styles

html {
  overflow-x: hidden;
}

body {
  font-family: $font;
  overflow-x: hidden;
}

p {
  padding: 0;
  margin: 0;
}

.container {
  max-width: 1400px;
  margin: 0 auto;
  @media screen and (max-width: 1440px) {
    max-width: 1170px;
  }
  @media screen and (max-width: 1170px) {
    padding-left: 30px;
    padding-right: 30px;
  }
  @media screen and (max-width: 640px) {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.grey {
  background: #FAFAFA;
}

// Default Styles

.default {
  &-button {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    text-decoration: none !important;
    padding: 8px 30px;
    background: #45A6FF;
    border-radius: 8px;
    &:hover {
      color: white;
      opacity: 0.8;
    }
  }
  &-title {
    font-family: Gilroy;
    font-style: normal;
    font-weight: bold;
    font-size: 45px;
    line-height: 135%;
    color: #181A1D;
    @media screen and (max-width: 1080px) {
      font-size: 40px;
    }
    @media screen and (max-width: 640px) {
      font-size: 30px;
    }
  }
  &-subtitle {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 135%;
    color: $gray;
    @media screen and (max-width: 640px) {
      font-size: 17px;
    }
    &.mini {
      font-size: 16px;
      @media screen and (max-width: 640px) {
        font-size: 12px;
      }
    }
  }
  &-link {
    font-family: $font;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 135%;
    color: $blue;
    display: flex;
    align-items: center;
    text-decoration: none !important;
    @media screen and (max-width: 640px) {
      font-size: 18px;
    }
    &:hover {
      opacity: 0.7;
    }
    &.decor:after {
      margin-left: 16px;
      content: "";
      display: block;
      width: 28.5px;
      height: 14px;
      background-image: url("../img/icons/arrow.svg");
      background-size: 100%;
    }
  }
}

// Header Styles

.header {
  padding: 30px 0;
  @media screen and (max-width: 450px) {
    padding: 15px 0;
  }
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .logo {
    img {
      width: 105px;
    }
  }
  @media screen and (max-width: 420px) {
    .default-button {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}

.menuBurger {
  img {
    width: 30px;
  }
}

.navigation {
  @media screen and (max-width: 768px) {
    display: none;
  }
  .nav-link {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: $black;
    margin-right: 50px;
    &:last-child {
      margin-right: 0;
    }
  }
}

// First Block Styles

.block01 {
  .container {
    padding-top: 162px;
    padding-bottom: 116px;
    background-image: url("../img/photos/woman.png");
    background-repeat: no-repeat;
    background-position: bottom right;
    @media screen and (max-width: 980px) {
      padding-bottom: 350px;
    }
    @media screen and (max-width: 640px) {
      padding-top: 100px;
      background-size: 90%;
    }
    @media screen and (max-width: 500px) {
      padding-bottom: 240px;
    }
    @media screen and (max-width: 450px) {
      padding-top: 50px;
      padding-bottom: 210px;
    }
  }

  .text-block {
    max-width: 721px;
    width: 100%;
    @media screen and (max-width: 1080px) {
      max-width: 650px;
    }
    @media screen and (max-width: 640px) {
      max-width: 312px;
    }
    .default-title {
      margin-bottom: 20px;
    }
    .default-subtitle {
      margin-bottom: 70px;
      @media screen and (max-width: 640px) {
        margin-bottom: 30px;
      }
    }
    .block01-button {
      display: inline-block;
      padding: 17px 50px;
      font-weight: 500;
      font-size: 22px;
      margin-bottom: 20px;
      @media screen and (max-width: 640px) {
        font-size: 17px;
        padding: 14px 30px;
      }
    }
    .mini {
      text-align: center;
      max-width: 252px;
      @media screen and (max-width: 640px) {
        max-width: 150px;
      }
    }
  }
}

.block-changed {
  .container {
    padding-top: 120px;
    padding-bottom: 144px;
  }
  @media screen and (max-width: 1100px) {
    .container {
      padding-bottom: 180px;
    }
    .text-block {
      max-width: 450px;
    }
  }
  @media screen and (max-width: 980px) {
    .container {
      padding-bottom: 400px;
    }
  }
  @media screen and (max-width: 470px) {
    .container {
      padding-top: 50px;
      padding-bottom: 350px;
    }
  }
  @media screen and (max-width: 375px) {
    .container {
      padding-bottom: 300px;
    }
  }
}

// Stats Styles

.stats {
  padding: 60px 0;
  border-bottom: 1.5px solid #F4F4F4;
  @media screen and (max-width: 768px) {
    padding: 40px 0;
  }
  .stats-list {
    display: flex;
    width: 100%;
    text-align: center;
    justify-content: space-between;
    @media screen and (max-width: 768px) {
      flex-wrap: wrap;
      margin-top: -20px;
      margin-bottom: -20px;
    }
    .stats-item {
      width: 25%;
      display: flex;
      align-items: center;
      justify-content: center;
      @media screen and (max-width: 768px) {
        width: 50%;
        margin-top: 20px;
        margin-bottom: 20px;
      }
      .wrap {
        max-width: 184px;
        width: 100%;
      }
      &:nth-child(2) {
        position: relative;
        &:after {
          content: "";
          display: block;
          position: absolute;
          left: 0;
          width: 1.5px;
          height: 87px;
          background: #F4F4F4;
          @media screen and (max-width: 768px) {
            display: none
          }
        }
        &:before {
          content: "";
          display: block;
          position: absolute;
          right: 0;
          width: 1.5px;
          height: 87px;
          background: #F4F4F4;
          @media screen and (max-width: 768px) {
            display: none
          }
        }
      }
      &:nth-child(3) {
        position: relative;
        &:before {
          content: "";
          display: block;
          position: absolute;
          right: 0;
          width: 1.5px;
          height: 87px;
          background: #F4F4F4;
          @media screen and (max-width: 768px) {
            display: none
          }
        }
      }
      span.num {
        font-family: $gilroy;
        font-style: normal;
        font-weight: bold;
        font-size: 40px;
        line-height: 48px;
        text-align: center;
        color: #181A1D;
        margin-bottom: 15px;
        display: block;
        @media screen and (max-width: 980px) {
          font-size: 30px;
          margin-bottom: 10px;
        }
      }
      p.default-subtitle {
        font-size: 18px;
        @media screen and (max-width: 980px) {
          font-size: 16px;
        }
      }
    }
  }
}

// Topics Styles

.topics {
  padding-top: 100px;
  padding-bottom: 44px;
  @media screen and (max-width: 640px) {
    padding-top: 60px;
    padding-bottom: 20px;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .text-block {
    max-width: 935px;
    width: 100%;
    text-align: center;
    margin: 0 auto 80px;
    .default-title {
      margin-bottom: 25px;
      @media screen and (max-width: 640px) {
        margin-bottom: 15px;
      }
    }
  }

  .topics-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: -50px;
    margin-bottom: -50px;
    @media screen and (max-width: 1110px) {
      justify-content: space-around;
      margin-top: -100px;
    }
    .topic {
      max-width: 354px;
      width: 100%;
      display: flex;
      align-items: flex-end;
      margin-top: 50px;
      margin-bottom: 50px;
      @media screen and (max-width: 1110px) {
        max-width: none;
        padding: 20px;
      }
      @media screen and (max-width: 640px) {
        margin-bottom: 15px !important;
      }
      &:nth-child(1) {
        img {
          width: 178px;
          @media screen and (max-width: 640px) {
            width: 90px;
          }
        }
      }
      &:nth-child(4) {
        max-width: 412px;
        @media screen and (max-width: 640px) {
          max-width: none !important;
        }
        img {
          width: 178px;
          @media screen and (max-width: 640px) {
            width: 90px;
          }
        }
      }
      &:nth-child(5) {
        img {
          width: 93px;
          @media screen and (max-width: 640px) {
            margin-right: 50px !important;
          }
          @media screen and (max-width: 640px) {
            width: 70px;
          }
        }
      }
      img {
        width: 120px;
        margin-right: 30px;
        @media screen and (max-width: 640px) {
          width: 90px;
        }
      }
      &-text {
        .title {
          font-family: $font;
          font-style: normal;
          font-weight: 500;
          font-size: 20px;
          line-height: 135%;
          color: $black;
          margin-bottom: 20px;
          display: block;
          @media screen and (max-width: 640px) {
            font-size: 18px;
            margin-bottom: 10px;
          }
        }
        p {
          font-size: 18px;
          @media screen and (max-width: 640px) {
            font-size: 16px;
          }
        }
      }
    }
  }
  
  .other-topics {
    display: inline-flex;
    text-align: center;
    margin-top: 70px;
  }
}

// Typical Block Styles

.typical-block {
  padding-top: 90px;
  padding-bottom: 100px;
  @media screen and (max-width: 570px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  &.left-pos {
    .container {
      flex-direction: row-reverse;
    }
    .text-block {
      margin-right: 0 !important;
    }
    .default-subtitle {
      max-width: 524px;
    }
  }
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 880px) {
      flex-direction: column-reverse !important;
      text-align: center;
      align-items: center;
    }
  }
  .text-block {
    max-width: 585px;
    width: 100%;
    margin-right: 30px;
    @media screen and (max-width: 570px) {
      max-width: none;
      padding: 0 30px;
      margin-right: 0;
    }
    .default-title {
      margin-bottom: 25px;
      @media screen and (max-width: 570px) {
        margin-bottom: 15px;
      }
    }
    .default-subtitle {
      margin-bottom: 80px;
      @media screen and (max-width: 570px) {
        margin-bottom: 50px;
      }
    }
  }
  .devices {
    display: flex;
    align-items: flex-end;
    @media screen and (max-width: 880px) {
      justify-content: center;
    }
    .device {
      margin-right: 45px;
      display: flex;
      flex-direction: column;
      align-items: center;
      @media screen and (max-width: 570px) {
        margin-right: 30px;
      }
      &:last-child {
        margin-right: 0;
      }
      span {
        font-family: $font;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 135%;
        color: $black;
        opacity: 0.5;
        @media screen and (max-width: 570px) {
          font-size: 16px;
        }
      }
    }
    .rect {
      background: #FFFFFF;
      border: 2.5px solid rgba(24, 26, 29, 0.3);
      box-sizing: border-box;
      border-radius: 5px;

    }
    .desktop-rect {
      width: 125px;
      height: 95px;
      margin-bottom: 17px;
      @media screen and (max-width: 570px) {
        width: 90px;
        height: 60px;
      }
    }
    .tablet-rect {
      width: 95px;
      height: 73px;
      margin-bottom: 28px;
      @media screen and (max-width: 570px) {
        width: 60px;
        height: 45px;
      }
    }
    .mobile-rect {
      width: 50px;
      height: 73px;
      margin-bottom: 28px;
      @media screen and (max-width: 570px) {
        width: 30px;
        height: 45px;
      }
    }
  }
  .block-image {
    @media screen and (max-width: 880px) {
      margin-bottom: 50px;
    }
    &.woman03 {
      img {
        height: 585px;
        @media screen and (max-width: 1150px) {
          height: 400px !important;
        }
        @media screen and (max-width: 570px) {
          height: 300px !important;
        }
      }
    }
    img {
      height: 612px;
      @media screen and (max-width: 1150px) {
        height: 400px !important;
      }
      @media screen and (max-width: 570px) {
        height: 300px !important;
      }
    }
  }
}

// Tutorial Styles

.tutorial {
  padding-top: 100px;
  padding-bottom: 120px;
  @media screen and (max-width: 1000px) {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  .container {

  }
  .default-title {
    text-align: center;
    margin-bottom: 80px;
  }

  .tutorial-steps {
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 100%;
    @media screen and (max-width: 1000px) {
      margin-left: -20px;
    }
    @media screen and (max-width: 866px) {
      flex-wrap: wrap;
      margin-left: 0;
      justify-content: space-around;
      margin-top: -20px;
      margin-bottom: -20px;
      &:after {
        display: none !important;
      }
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      left: 50px;
      right: 50px;
      top: 80px;
      height: 5px;
      background: #F5F5F5;
      z-index: 5;
      @media screen and (max-width: 1000px) {
        top: 50px;
        left: 90px;
        right: 90px;
      }
    }
  }
  .tutorial-step {
    position: relative;
    z-index: 6;
    display: flex;
    align-items: center;
    flex-direction: column;
    @media screen and (max-width: 866px) {
      width: 50%;
      margin-bottom: 40px;
      margin-top: 40px;
    }
    span {
      text-align: center;
      font-family: $font;
      font-style: normal;
      font-weight: normal;
      font-size: 22px;
      line-height: 135%;
      color: $black;
      opacity: 0.5;
      max-width: 228px;
      width: 100%;
      margin-top: 50px;
      @media screen and (max-width: 1000px) {
        font-size: 16px;
      }
      @media screen and (max-width: 450px) {
        font-size: 14px;
        margin-top: 20px;
        max-width: 80%;
      }
    }
    img {
      width: 160px;
      @media screen and (max-width: 1000px) {
        width: 100px;
      }
      @media screen and (max-width: 450px) {
        width: 50px;
      }
    }
  }
}

// Specialists Styles

.specialists {
  padding-top: 100px;
  padding-bottom: 120px;
  @media screen and (max-width: 640px) {
    padding-top: 70px;
    padding-bottom: 50px;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .text-block {
    text-align: center;
    max-width: 950px;
    margin-bottom: 80px;
    .default-title {
      margin-bottom: 25px;
    }
  }

  @media screen and (max-width: 640px) {
    .default-link {
      margin-top: 50px;
    }
  }
  @media screen and (max-width: 500px) {
    .default-link {
      font-size: 16px;
      max-width: 60%;
      text-align: center;
      &:after {
        display: none;
      }
    }
  }

  .specialists-group {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 70px;
    @media screen and (max-width: 1100px) {
      flex-wrap: wrap;
      justify-content: space-around;
      margin: -30px;
    }
    .specialist {
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 330px;
      @media screen and (max-width: 1100px) {
        margin: 30px;
      }
      @media screen and (max-width: 500px) {
        margin-bottom: 40px;
      }
      .name {
        font-family: $font;
        font-style: normal;
        font-weight: 500;
        font-size: 22px;
        line-height: 135%;
        text-align: center;
        color: $black;
        margin-bottom: 15px;
        @media screen and (max-width: 500px) {
          font-size: 18px;
          margin-bottom: 10px;
        }
      }
      .desc {
        font-family: $font;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 135%;
        text-align: center;
        color: $gray;
        @media screen and (max-width: 500px) {
          font-size: 16px;
        }
      }
      img {
        width: 330px;
        margin-bottom: 30px;
        @media screen and (max-width: 500px) {
          width: 80%;
        }
      }
    }
  }
}

// Reviews Styles

.reviews {
  padding: 120px 0;
  @media screen and (max-width: 640px) {
    padding: 60px 10px 80px;
  }

  .swiper-container {
    position: relative;
    padding-bottom: 100px;
    @media screen and (max-width: 640px) {
      padding-bottom: 50px;
    }
    .swiper-pagination {
      position: absolute;
      bottom: 0;
    }
    .swiper-pagination-bullet {
      background: #EAE8E8;
      width: 25px;
      height: 25px;
      margin-right: 23px !important;
      @media screen and (max-width: 640px) {
        width: 15px;
        height: 15px;
        margin-right: 15px;
      }
      &-active {
        background: #45A6FF;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
  .default-title {
    text-align: center;
    margin-bottom: 120px;
    @media screen and (max-width: 640px) {
      margin-bottom: 60px;
    }
  }
  .review {
    border: 1.5px solid rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    border-radius: 10px;
    padding: 50px 28px 0 28px;
    padding-bottom: 50px;
    display: flex;
    align-items: flex-start;
    min-height: 215px !important;
    @media screen and (max-width: 640px) {
      padding: 25px 14px;
      min-height: 100px !important;
    }
    img {
      width: 100px;
      margin-right: 25px;
      @media screen and (max-width: 640px) {
        width: 60px;
      }
    }
    .name {
      font-family: $font;
      font-style: normal;
      font-weight: 500;
      font-size: 22px;
      line-height: 135%;
      color: #000000;
      margin-bottom: 14px;
      display: block;
      @media screen and (max-width: 640px) {
        font-size: 17px;
      }
    }
    .review-body {
      font-family: $font;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 135%;
      color: #000000;
      opacity: 0.5;
      @media screen and (max-width: 640px) {
        font-size: 16px;
      }
    }
  }
}

// FAQ Styles

.faq {
  padding-bottom: 120px;
  @media screen and (max-width: 700px) {
    padding-bottom: 60px;
  }
  .default-title {
    text-align: center;
    margin-bottom: 73px;
    @media screen and (max-width: 700px) {
      margin-bottom: 50px;
    }
  }
  
  .tabs {
    display: flex;
    flex-direction: column;
  }
  
  .tab {
    width: 100%;
    // Text
    font-family: $font;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 135%;
    color: $black;
    border-bottom: 1.5px solid #F4F4F4;
    padding-bottom: 0;
    @media screen and (max-width: 700px) {
      font-size: 17px;
    }
    .text-wrapper {
      padding-bottom: 35px;
      display: block;
    }
    &.active {
      .tab-header {
        border-bottom: none;
      }
      .tab-content {
        max-height: 500px;
        transition: max-height 1.5s ease 0s
      }
    }
    &:first-child {
      .tab-header {
        border-top: 1.5px solid #F4F4F4;
      }
    }
    &-header {
      display: flex;
      justify-content: space-between;
      padding: 26px 0px;
      align-items: center;
      @media screen and (max-width: 700px) {
        padding: 20px 0;
        .toggle-button {
          margin-left: 20px;
        }
      }
    }
    &-content {
      max-height: 0px;
      font-family: $font;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 165%;
      color: #181A1D;
      opacity: 0.5;
      overflow: hidden;
      transition: max-height 0.5s ease 0s;
      @media screen and (max-width: 700px) {
        font-size: 16px;
      }
    }
  }
}

// Last Block Styles

.last-block {
  padding: 120px 0;
  text-align: center;
  @media screen and (max-width: 700px) {
    padding: 60px 0;
  }
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .default-title {
    width: 100%;
    max-width: 585px;
    margin-bottom: 25px;
    @media screen and (max-width: 700px) {
      margin-bottom: 15px;
    }
  }
  .default-subtitle {
    margin-bottom: 70px;
    font-weight: normal;
    max-width: 790px;
    @media screen and (max-width: 700px) {
      margin-bottom: 40px;
    }
  }

  .default-button {
    font-size: 22px;
    padding: 18px 50px;
    @media screen and (max-width: 700px) {
      font-size: 17px;
      padding: 14px 35px;
    }
  }
}

// Footer Styles

.footer {
  padding-top: 102px;
  padding-bottom: 140px;
  @media screen and (max-width: 556px) {
    padding-top: 70px;
    padding-bottom: 70px;
  }
  .container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @media screen and (max-width: 1180px) {
      flex-wrap: wrap;
      padding: 0 20px;
      justify-content: space-around;
    }
    @media screen and (max-width: 768px) {
      justify-content: space-between;
      align-items: center;
    }
    @media screen and (max-width: 556px) {
      flex-direction: column;
    }
  }

  .nav-link, .doc {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #181A1D;
    margin-bottom: 15px;
  }

  .about-company {
    display: flex;
    flex-direction: column;
    margin-right: 20px;
    @media screen and (max-width: 1160px) {
      width: 100%;
      align-items: center;
      margin-bottom: 50px;
      margin-right: 0;
    }
    .logo {
      margin-bottom: 30px;
      img {
        width: 105px;
      }
    }
    .about-info {
      font-family: $font;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 19px;
      color: $gray;
      @media screen and (max-width: 1160px) {
        text-align: center;
      }
      span {
        display: inline-block;
        margin-bottom: 15px;
      }
    }
  }

  .footer-navigation {
    display: flex;
    flex-direction: column;
    @media screen and (max-width: 556px) {
      text-align: center;
      margin-bottom: 30px;
    }
  }

  .footer-documents {
    display: flex;
    flex-direction: column;
    @media screen and (max-width: 556px) {
      text-align: center;
      margin-bottom: 30px;
    }
  }

  .email {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    color: #181A1D !important;
    display: flex;
    flex-direction: column;
    @media screen and (max-width: 768px) {
      width: 100%;
      margin-top: 30px;
      justify-content: center;
      text-align: center;
    }

    a {
      color: #181A1D !important;
    }
    .label {
      display: inline-block;
      opacity: 0.5;
      margin-top: 15px;
    }
  }
}

.bottom-footer {
  padding: 24px 0;
  font-family: $font;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #181A1D;
  border-top: 1.5px solid #F4F4F4;
}